/**
  * Configuration variable
  */
header {
  display: flex;
  position: fixed;
  width: 100%;
  height: 60px;
  align-items: center;
  box-shadow: 0px 1px rgba(0, 0, 0, 0.1);
  font-size: 1.2em;
  -moz-transition: height 1s ease;
  -webkit-transition: height 1s ease;
  -o-transition: height 1s ease;
  transition: height 1s ease;
  z-index: 999; }
  header .blur {
    display: inline-block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.9);
    z-index: -1;
    top: 0; }
  header nav {
    position: absolute;
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    padding: 0 20px;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    top: 0; }
    header nav a {
      color: #333; }
      header nav a.active {
        color: #1e70bf; }
    header nav ul a {
      padding: 0 5px; }
    header nav div,
    header nav ul,
    header nav li {
      display: inline-block; }
