/**
  * Configuration variable
  */
.container {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .container > div {
    padding: 20px 20px 40px;
    margin: 20px;
    max-width: 1200px;
    width: 100%; }
    .container > div:nth-child(1) {
      padding-top: 40px; }
    .container > div:not(:nth-last-child(1)) {
      border-bottom: 1px solid #bbb; }
    .container > div:nth-last-child(1) {
      margin-bottom: 0; }
